<template>
<div>
  <v-form v-show="online && !submittedCode" v-model="validCode" ref="formCode" @submit.prevent>
    <v-row>
      <v-col cols="12">
	<v-text-field v-model="fiscalCode" :rules="nameRules" label="Codice fiscale" required></v-text-field>
      </v-col>
    </v-row>
</v-form>
<v-form v-show="!online || submittedCode" v-model="valid" ref="form">
  <v-row>
    <v-col cols="12" md="6">
      <v-text-field v-model="firstname" :rules="nameRules" :counter="30" label="Nome" required></v-text-field>
    </v-col>
    <v-col cols="12" md="6">
      <v-text-field v-model="lastname" :rules="nameRules" :counter="30" label="Cognome" required></v-text-field>
    </v-col>
    <v-col cols="12" md="6">
      <v-text-field v-model="fiscalCode" :rules="fiscalRules" label="Codice fiscale"></v-text-field>
    </v-col>
    <v-col cols="12" md="6">
      <v-text-field v-model="email" :rules="emailRules" label="E-mail" validate-on-blur required></v-text-field>
    </v-col>
    <v-col cols="12" md="6">
      <v-menu ref="menu" v-model="menu" :close-on-content-click="false" transition="scale-transition" offset-y min-width="290px">
	<template v-slot:activator="{ on }">
	  <v-text-field v-model="birthdate" :rules="dateRules" label="Data di nascita" readonly v-on="on"></v-text-field>
	</template>
	<v-date-picker ref="picker" v-model="birthdate" :max="new Date().toISOString().substr(0, 10)" min="1930-01-01" @change="save"></v-date-picker>
      </v-menu>
    </v-col>
    <v-col cols="12" md="6">
      <v-text-field v-model="birthplace" :rules="nameRules" :counter="30" label="Luogo di nascita" required></v-text-field>
    </v-col>
    <v-col cols="12" md="6">
      <v-text-field v-model="city" :rules="nameRules" :counter="30" label="Città di residenza" required></v-text-field>
    </v-col>
    <v-col cols="12" md="6">
      <v-text-field v-model="phone" :rules="phoneRules" label="Numero di Telefono" validate-on-blur required></v-text-field>
    </v-col>
  </v-row>
</v-form>
</div>
</template>

<script>
import { mapState } from 'vuex';
import loadFromStore from "../mixins/loadFromStore.js";
import isEmpty from 'lodash/isEmpty';

export default {
    mixins: [loadFromStore],
    data: () => ({
	valid: false,
	validCode: false,
	submittedCode: false,
	firstname: '',
	lastname: '',
	fiscalCode: '',
	nameRules: [
            v => !!v || 'Text is required',
            v => v.length <= 30 || 'Text must be less than 30 characters',
	],
	fiscalRules: [
            v => v.length <= 30 || 'Text must be less than 30 characters',
	],
	email: '',
	emailRules: [
            v => !!v || 'E-mail is required',
	    v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
	],
	pickerSelected: false,
	menu: false,
	birthdate: null,
	dateRules: [v => v != null || 'Date is required'],
	birthplace: '',
	city: '',
	phone: '',
	phoneRules: [v => !!v || 'Phone number is required',
		     v => /^\d+$/.test(v.replace(/\s/g,'').slice(1)) || 'Insert a valid phone number'],
    }),
    watch: {
	menu(val) {
	    // Enters 2 times: when first clicking to birthdate picker and when leaving.
	    // The following if/else statements override the default behaviour of giving an error
	    // before having finished to insert the whole birthdate.
	    if(this.pickerSelected) {
		this.pickerSelected = false;
		if(this.birthdate === '') {
		    this.birthdate = null;
		}
	    } else {
		this.birthdate = '';
		this.pickerSelected = true;
	    }
            val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
	},
    },
    methods: {
	getUserFromCode(code) {
	    this.$http.get(`/user/${code}`)
		.then(response => {
		    let registration = response.data;
		    console.log(response)
		    console.log(registration);
		    this.loadFromStore(this.$data, registration);
		})
		.catch(error => {
		    console.log(error);
		})
		.then(() => {
		    this.submittedCode = true;
		});
	},
	validate() {
	    if(!this.submittedCode && this.online && this.$refs.formCode.validate()) {
		this.getUserFromCode(this.fiscalCode);
	    } if(this.submittedCode || !this.online) {
		if (this.$refs.form.validate()) {
	    	    // Entering if form is valid
		    console.log("IN THE LAST SUBMIT");
	    	    this.snackbar = true;
	    	    let result = (({ firstname, lastname, fiscalCode, email, birthdate, birthplace, city, phone }) =>
	    			  ({ firstname, lastname, fiscalCode, email, birthdate, birthplace, city, phone}))(this);
	    	    console.log(result);
	    	    this.$store.commit('setRegistration', result);
	    	    console.log(this.$store.state.registration);
		}
	    }
	},
	save(birthdate) {
            this.$refs.menu.save(birthdate);
	},
    },
    computed: {
	    ...mapState(['registration', 'online']),
    },
    mounted() {
	// Loading registration values from vuex if already present
	this.loadFromStore(this.$data, this.registration);
	if(!isEmpty(this.registration)) {
	    this.submittedCode = true;
	}
    },
}
</script>
