<template>
<v-app>
  <v-container>
    <!-- <p>Ciao {{$route.params.id}}</p> -->
    <!-- <p>Ciao {{this.fiscalCode}}</p> -->
    <!-- <p>{{this.x}}</p> -->
    <v-row>
      <v-col cols="12">
	<v-card class="mx-auto"  >
	  <v-card-title><strong>{{user.firstname}} {{user.lastname}}</strong></v-card-title>
	  <v-list-item v-for="(v, n) in userInfo" :key="n">{{n}}: {{v}}</v-list-item>
	</v-card>
      </v-col>
      
      <v-col v-for="(value, index) in checkups" :key="index" cols="12">
	<v-hover v-slot:default="{ hover }">
	<v-card class="mx-auto" :elevation="hover ? 6 : 2" @click="viewCheckup(value._id)">
	  <v-card-title><strong>Checkup {{index+1}}</strong></v-card-title>
	  <v-list-item>Data: {{toDate(value.timestamp)}}</v-list-item>
	  <v-list-item>Ore: {{toTime(value.timestamp)}}</v-list-item>
	</v-card>
	</v-hover>
      </v-col>
      
      
    </v-row>
  </v-container>
  </v-app>
</template>

<script>
const months = ["gennaio", "febbraio", "marzo", "aprile", "maggio", "giugno",
		"luglio", "agosto", "settembre", "ottobre", "novembre", "dicembre"];

export default {
    data: () => ({
	x: 0,
	fiscalCode: '',
	user: {},
	checkups: {},
    }),
    methods: {
	loadCheckupsData(code) {
	    let self = this;
	    this.$http.get(`/checkups/${code}`)
		.then(response => {
		    self.checkups = response.data;
		})
		.catch(() => {
		});
	},
	loadUserData(code) {
	    let self = this;
	    this.$http.get(`/user/${code}`)
		.then(response => {
		    self.user = response.data;
		    self.loadCheckupsData(code);
		})
		.catch(() => {
		});
	},
	toDate(timestamp) {
	    let x = new Date(timestamp);
	    return '' + x.getDate() + ' ' + months[x.getMonth()] + ' ' + x.getFullYear();
	},
	addZero(hoursOrMins) {
	    if(hoursOrMins < 10) {
		return '0' + hoursOrMins;
	    }
	    return hoursOrMins;
	},
	toTime(timestamp) {
	    let x = new Date(timestamp);
	    return '' + this.addZero(x.getHours()) + ':' + this.addZero(x.getMinutes());
	},
	viewCheckup(checkup_id) {
	    this.$router.push({ name: 'checkup', params: {id: checkup_id} });
	}
    },
    computed: {
	userInfo: function () {
	    let result = (({ fiscalCode, email, birthdate, birthplace, city, phone }) =>
	    		  ({ fiscalCode, email, birthdate, birthplace, city, phone}))(this.user);
	    return result;
	},
    },
    created() {
	this.fiscalCode = this.$route.params.id;
	this.loadUserData(this.$route.params.id);
    },
    watch: {
	$route (to){
            this.x += Math.random();
	    this.fiscalCode = to.params.id;
	    this.loadUserData(to.params.id);
	}
    } 
}
</script>
