<!-- Dati Antropometrici -->
<template>
  <v-form v-model="valid" ref="form">
  <h2>Obiettivo personale</h2>
  <v-divider></v-divider>
  <v-row>
    <v-col cols="12">
      <v-select v-model="objectiveType" :items="objectiveOptions" :rules="[v => !!v || 'Item is required']" label="Select" required single-line></v-select>
    </v-col>
  </v-row>
  </v-form>
</template>

<script>
import { mapState } from 'vuex';
import loadFromStore from "../mixins/loadFromStore.js"
  
export default {
    mixins: [loadFromStore],
    data: () => ({
	valid: false,
	objectiveType: null,
	objectiveOptions: ['Slim','Antiage','Energize','Sleep', 'Detox', 'Strong immuno'],
	objectiveSuggestionArray: [['Body', 'Nutrizione', 'Detox', 'Stress', 'Sleep'],
				   ['Body', 'Nutrizione', 'Detox', 'Stress', 'Skin'],
				   ['Body', 'Nutrizione', 'Sleep', 'Stress'],
				   ['Body', 'Nutrizione', 'Sleep', 'Stress'],
				   ['Body', 'Nutrizione', 'Sleep', 'Stress', 'Detox'],
				   ['Body', 'Nutrizione']],
	objectiveSuggestion: null,
    }),
    methods: {
	computeResult() {
	    this.objectiveSuggestion = this.objectiveSuggestionArray[this.objectiveOptions.indexOf(this.objectiveType)];
	},
	validate() {
            if (this.$refs.form.validate()) {
		this.computeResult();
		let result = (({ objectiveType, objectiveSuggestion }) => ({ objectiveType, objectiveSuggestion }))(this);
		this.$store.commit('setObjective', result);
            }
	},
    },
    computed: {
	    ...mapState(['objective']),
    },
    mounted() {
	// Loading skin values from vuex if already present
	this.loadFromStore(this.$data, this.objective);
    },
}
</script>
