import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import About from "../views/About.vue";
import User from "../views/User.vue";
import Checkup from "../views/Checkup.vue";

Vue.use(VueRouter);

const routes = [
    {
	path: "/",
	name: "home",
	component: Home
    },
    {
	path: '/about',
	name: 'about',
	component: About,
	props(route) {
	    return  route.query || {};
	}
    },
    {
	path: "/user/:id",
	name: "user",
	component: User
    },
    {
	path: "/checkup/:id",
	name: "checkup",
	component: Checkup
    },
];

const router = new VueRouter({
//    mode: 'history',  // When enabling this there are problems in accessing directly user/:id from url.
    routes
});

export default router;
