<template>
<v-app>
  <v-container>
    <StepResult v-bind="checkup" />
  </v-container>
  </v-app>
</template>

<script>
export default {
    data: () => ({
	checkup_id: '',
	checkup: {},
    }),
    methods: {
	loadCheckupData(code) {
	    let self = this;
	    this.$http.get(`/checkup/${code}`)
		.then(response => {
		    self.checkup = response.data;
		})
		.catch(() => {

		});
	},
    },
    computed: {
    },
    created() {
	this.checkup_id = this.$route.params.id;
	this.loadCheckupData(this.$route.params.id);
    },
    watch: {
	$route (to){
	    this.checkup_id = to.params.id;
	    this.loadCheckupData(to.params.id);
	}
    } 
}  
</script>
