export default {
    methods: {
	// Takes as input an answers array and options array of arrays
	// to compute a longevity score
	computeLongevity(answers, options) {
	    let scoreSum = 0;
	    for(let i = 0; i < answers.length; i++) {
		let currOpt = Array.isArray(options[0]) ? options[i] : options;
		let currAns = answers[i];
		this.$store.commit('setOptionsScore', currOpt.length);
		scoreSum += this.$store.getters.getScoreFromOptions(currOpt.length, currOpt.indexOf(currAns));
	    }
	    return scoreSum / answers.length;
	}
    }
};
