<!-- Dati Antropometrici -->
<template>
  <v-form v-model="valid" ref="form">
      <v-row>
	<v-col cols="12" md="12">
          <!-- <v-select v-model="skinType" :items="skinOptions" :rules="[v => skinOptions.includes(v) || 'Item is required']" label="Skin" required></v-select> -->
	  <v-select v-model="skinSensitivity" :items="skinSensitivityOptions" :rules="[v => skinSensitivityOptions.includes(v) || 'Item is required']" label="Pelle sensibile" required></v-select>
	  <v-select v-model="skinDehydratation" :items="skinDehydratationOptions" :rules="[v => skinDehydratationOptions.includes(v) || 'Item is required']" label="Pelle disidratata" required></v-select>
	  <v-select v-model="skinImpurities" :items="skinImpuritiesOptions" :rules="[v => skinImpuritiesOptions.includes(v) || 'Item is required']" label="Pelle impura" required></v-select>
	  <v-select v-model="skinAging" :items="skinAgingOptions" :rules="[v => skinAgingOptions.includes(v) || 'Item is required']" label="Segni d'invecchiamento" required></v-select>
        </v-col>
      </v-row>
  </v-form>
</template>

<script>
import { mapState } from 'vuex';
import loadFromStore from "../mixins/loadFromStore.js"
  
export default {
    mixins: [loadFromStore],
    data: () => ({
	valid: false,
	// skinType: null,
	// skinOptions: ['secca', 'rughe fisse', 'rughe di espressione' , 'macchie', 'normale' , 'pelle grassa',],
	skinSensitivity: null,
	skinSensitivityOptions: ['arrossamenti', 'pelle stressata', 'con fastidi', 'irritazioni',
				 'iperemie', 'prurito/eruzioni', 'pelle sottile/pallida'],
	skinDehydratation: null,
	skinDehydratationOptions: ['rughe/rughette di disidratazione', 'pelle ruvida/secca', 'pelle spenta, mancanza di luminosità',
				   'mancanza di morbidezza', 'desquamazioni', 'pelle stressata', 'pelle fragile/reattiva'],
	skinImpurities: null,
	skinImpuritiesOptions: ['pelle oleosa/mista', 'pelle spessa', 'pelle lucida (zona T)', 'Carnagione irregolare',
				'pori dilatati', 'comedoni/microcisti', 'pustole/papule'],
	skinAging: null,
	skinAgingOptions: ['mancanza di elasticità/tonicità', 'rughe profonde', 'mancanza di luminosità', 'mancanza di densità, pelle sottile',
			   'pori dilatati', 'contorni irregolari', 'macchie di pigmentazione'],
    }),
    methods: {
	validate() {
            if (this.$refs.form.validate()) {
		let result = (({ skinSensitivity, skinDehydratation, skinImpurities, skinAging }) => ({ skinSensitivity, skinDehydratation, skinImpurities, skinAging }))(this);
		this.$store.commit('setSkin', result);
            }
	},
    },
    computed: {
	    ...mapState(['skin']),
    },
    mounted() {
	// Loading skin values from vuex if already present
	this.loadFromStore(this.$data, this.skin);
    },
}
</script>
