<style scoped>
  .suggestedCard {
      border-left: 5px solid red !important;
  }
</style>

<template>
<v-app>  
  <v-stepper v-model="e1">
    
    <v-stepper-items>
      <!-- Debugging step to print content of vuex store -->
      <v-stepper-content step="-2">
	<v-container>
	  <v-row>
	    <v-col v-for="(value, name) in getSubmitData" :key="name" cols="12">
	      <v-card class="mx-auto"  >
		<v-card-title><strong>{{name}}</strong></v-card-title>
		<v-list-item v-for="(v, n) in value" :key="n">{{n}}: {{v}}</v-list-item>
	      </v-card>
	    </v-col>
	  </v-row>
	</v-container>
	<v-container>
	  <v-btn  @click="e1=0">Previous</v-btn>
	</v-container>
      </v-stepper-content>
      <!-- Result step -->
      <v-stepper-content step="-1">
	<v-app-bar color="white" fixed elevate-on-scroll>
	  <v-btn icon @click="goToPrevious()">
            <v-icon>mdi-arrow-left</v-icon>
          </v-btn>
	  <v-toolbar-title>Risultati</v-toolbar-title>
	</v-app-bar>
	<v-container style="padding-top: 50px">
	  <StepLast />
	</v-container>
      </v-stepper-content>
      <!-- The control panel step with all the tests -->
      <v-stepper-content step="0">
      	<v-container>
      	  <v-row>
	    <!-- The header card containing user information -->
	    <v-col cols="12">
      	      <!-- <v-card class="mx-auto" :color="item.color" dark :elevation="hover ? 12 : 2"> -->
      	      <v-card class="mx-auto" color="#385F73" dark>
      		<div class="d-flex flex-no-wrap justify-space-between">
      		  <div>
                    <v-card-title class="headline"><v-icon left>mdi-account</v-icon>{{registration.firstname}} {{registration.lastname}}</v-card-title>
                    <v-card-subtitle>
		      <v-icon left>mdi-checkbox-marked-circle</v-icon>Completed: {{completed}} / {{items.length}}
		      <br />
		      <v-icon left>mdi-flash</v-icon>Longevity level: {{progress}}%
		    </v-card-subtitle>
      		  </div>
      		</div>
		<v-progress-linear v-if="progress > 0" active background-opacity="0.27" buffer-value="100" height="16" determinate  :value="progress" :color="barColor">      
		  <strong>{{progress}}%</strong>
		</v-progress-linear>
      	      </v-card>
	    </v-col>
	    <!-- Generating cards for all the individual tests inside the control panel -->
            <v-col v-for="(item, i) in items" :key="i" cols="12" sm="6" md="4">
      	      <v-hover v-slot:default="{ hover }">
      		<v-card class="mx-auto" v-bind:class="{suggestedCard : item.suggested}" :elevation="hover ? 12 : 2" @click="clickCard(item.no)">
      		  <div class="d-flex flex-no-wrap justify-space-between">
      		    <div>
                      <v-card-title class="headline" v-text="item.title"></v-card-title>
                      <!-- <v-card-subtitle v-text="item.artist"></v-card-subtitle> -->
		      <v-card-subtitle v-if="(item.completed)">
			Completed<v-icon right>mdi-checkbox-marked-circle</v-icon>
		      </v-card-subtitle>
		      <v-card-subtitle v-else>
			Not Completed<v-icon right>mdi-checkbox-marked-circle-outline</v-icon>
		      </v-card-subtitle>
      		    </div>
      		  </div>
      		</v-card>
      	      </v-hover>
            </v-col>
	    <!-- The buttons to submit or cancel everything -->
	    <v-container>
	      <v-container>
		<v-row justify="space-between">
		  <!-- Delete Button -->
		  <v-btn @click.stop="deleteDialog = true">Delete</v-btn>
		  <v-dialog v-model="deleteDialog" max-width="290">
		    <v-card>
		      <v-card-title style="word-break: normal;"><span class="headline">Are you sure you want to delete the current session?</span></v-card-title>
		      <v-card-actions>
			<v-spacer></v-spacer>
			<v-btn color="green darken-1" text @click="deleteDialog = false">
			  Disagree
			</v-btn>
			<v-btn color="green darken-1" text @click="deleteCurrentSession()">
			  Agree
			</v-btn>
		      </v-card-actions>
		    </v-card>
		  </v-dialog>
		  <!-- Submit Button -->
		  <v-btn color="primary" dark @click.stop="submitDialog = true">Submit</v-btn>
		  <v-dialog v-model="submitDialog" max-width="290">
		    <v-card>
		      <v-card-title style="word-break: normal;"><span class="headline">Are you sure you want to submit?</span></v-card-title>
		      <v-card-actions>
			<v-spacer></v-spacer>
			<v-btn color="green darken-1" text @click="submitDialog = false">
			  Disagree
			</v-btn>
			<v-btn color="green darken-1" text @click="submitData()">
			  Agree
			</v-btn>
		      </v-card-actions>
		    </v-card>
		  </v-dialog>
		</v-row>
	      </v-container>
	    </v-container>
      	  </v-row>
      	</v-container>
      </v-stepper-content>
      <!-- The individual tests bodies -->
      <v-stepper-content v-for="(item, i) in items" :key="i" :step="item.no">
	<v-app-bar color="white" fixed elevate-on-scroll>
	    <v-btn v-if="(item.no != 1 || landed)" icon @click="goToPrevious()">
              <v-icon>mdi-arrow-left</v-icon>
            </v-btn>
	    <v-toolbar-title>{{item.title}}</v-toolbar-title>
	  </v-app-bar>
      	<v-container style="padding-top: 50px">
      	    <component v-bind:is="item.step" :ref="item.step" />
      	  <v-container>
      	    <v-row justify="end">
      	      <v-btn color="primary" @click="()=>validate(item.step)">Continue</v-btn>
      	    </v-row>
      	  </v-container>
      	</v-container>
      </v-stepper-content>

    </v-stepper-items>

  </v-stepper>
</v-app>
</template>


<script>

import { mapState, mapGetters } from 'vuex';
import loadFromStore from "../mixins/loadFromStore.js";

export default {
    mixins: [loadFromStore],
    data () {
	return {
	    e1: 1,
	    progress: 0,
	    landed: false,
	    items: [
		{title: 'Registration', no: 1, step: "Step1", completed: false, suggested: false},
		{title: 'Privacy', no: 2, step: "Step2", completed: false, suggested: false},
		{title: 'Objective', no: 3, step: "StepObjective", completed: false, suggested: false},
		{title: 'Body', no: 4, step: "Step3", completed: false, suggested: false},
		{title: 'Skin', no: 5, step: "Step4", completed: false, suggested: false},
		{title: 'Detox', no: 6, step: "Step5", completed: false, suggested: false},
		{title: 'Stress', no: 7, step: "Step6", completed: false, suggested: false},
		{title: 'Sleep', no: 8, step: "Step7", completed: false, suggested: false},
		{title: 'Nutrizione', no: 9, step: "Step8", completed: false, suggested: false},
	    ],
	    submitDialog: false,
	    deleteDialog: false,
	    mandatorySteps : 3,
	}
    },
    methods: {
	validate(comp) {
	    let refToValidate = this.$refs[comp][0];
	    refToValidate.validate();
	    if (refToValidate.valid) {
		if(this.e1 < this.mandatorySteps && !this.landed){
		    this.items[this.e1 - 1].completed = true;
                    this.e1 += 1;
		} else {
		    if(this.e1 == this.mandatorySteps) {
			for(var i = 0; i < this.items.length; i++) {
			    // refToValidate can be used instead of this.objective
			    var x = this.objective.objectiveSuggestion.includes(this.items[i].title);
			    this.items[i].suggested = x;
			}
		    }
		    if(refToValidate.hasOwnProperty('longevity')) {
			this.items[this.e1 - 1].longevity = refToValidate.longevity;
			this.computeProgress();
		    }
		    this.landed = true;
		    this.items[this.e1 - 1].completed = true;
		    this.e1 = 0;
		}
		this.commitToVuex();
		this.scrollToTop();
	    }
	},
	goToPrevious() {
	    this.e1 = this.landed ? 0 : this.e1 - 1;
	    this.commitToVuex();
	    this.scrollToTop();
	},
	scrollToTop() {
            window.scrollTo(0,0);
        },
	clickCard(no) {
	    this.e1 = no;
	    this.commitToVuex();
	    this.scrollToTop();
	},
	computeProgress() {
	    let progress = 0, entries = 0;
	    for(let i = 0; i < this.items.length; i++) {
		if(this.items[i].hasOwnProperty('longevity')) {
		    progress += this.items[i].longevity;
		    entries += 1;
		}
	    }
	    // Instead of showing a 0 percentage bar put 1 instead. Just for graphic purposes.
	    this.progress = (progress === 0 && entries > 0) ? 1 : Math.ceil((progress / entries) * 100);
	    this.commitToVuex();
	},
	commitToVuex() {
	    let result = (({e1, progress, landed, items}) => ({e1, progress, landed, items}))(this);
	    this.$store.commit('setControlPanel', result);
	},
	deleteCurrentSession() {
	    // delete all current session data, from vuex as well.
	    this.deleteDialog = false;
	    this.$store.commit('deleteEverything');
	    location.reload();
	},
	isOnline() {
	    this.$http.get("/online")
		.then(() => {
		    this.$store.commit('setOnline', true);
		})
		.catch(() => {
		    this.$store.commit('setOnline', false);
		});
	},
	postData() {
	    // let self = this;
	    this.$http.post('/submit', this.getSubmitData)
		.then(function () {
		    // self.e1 = -1;
		})
		.catch(function () {
		});
	},	
	submitData() {
	    // method to submit all of the data
	    this.postData();
	    this.e1 = -1;
	    this.commitToVuex();
	    this.$store.commit('setSupplements', ["detox", "slim", "dren", "antiage", "collagene", "stress relieve", "sleep better"]);
	    this.submitDialog = false;
	},
    },
    computed: {
	completed: function () {
	    let compl = 0;
	    for(let i = 0; i < this.items.length; i++) {
		if(this.items[i].completed) {
		    compl += 1;
		}
	    }
	    return compl;
	},
	    ...mapState(['registration', "controlPanel", "objective"]),
	    ...mapGetters(['getSubmitData']),
	barColor: function () {
	    if(this.progress < 33) {
		return "red";
	    }
	    if(this.progress < 66) {
		return "yellow";
	    }
	    return "green";
	},
    },
    created() {
	this.isOnline();
    },
    mounted() {
	// Loading control panel values from vuex if already present
	this.loadFromStore(this.$data, this.controlPanel);
    }
}
</script>
