<!-- Dati Antropometrici -->
<style scoped>
  .subform {
      padding-top: 60px;
  }
</style>

<template>
<v-form v-model="valid" ref="form">
  <h2>Dati corporei</h2>
  <v-divider></v-divider>
      <v-row ref="subform1">
	<v-col cols="12" md="6">
          <v-select v-model="sex" :items="sexOptions" :rules="[v => !!v || 'Item is required']" label="Sesso" required></v-select>
        </v-col>
	<v-col cols="12" md="6">
          <v-select v-model="bodyType" :items="bodyOptions" :rules="[v => !!v || 'Item is required']" label="Morfotipo" required></v-select>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field v-model.number="height" type="number" :rules="heightRules" label="Altezza (cm)" required></v-text-field>
        </v-col>
	<v-col cols="12" md="6">
          <v-text-field v-model.number="weight" type="number" :rules="weightRules" label="Peso (kg)" required></v-text-field>
        </v-col>
      </v-row>
      <div v-show="e1 >= 2" ref="subform2" class="subform">
      <h2>Dati corporei aggiuntivi</h2>
      <v-row>
	<v-col cols="12" md="4">
          <v-text-field v-model.number="neck" :rules="neckRules" label="Collo diametro (cm)"></v-text-field>
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field v-model.number="waist" :rules="waistRules" label="Vita diametro (cm)"></v-text-field>
        </v-col>
	<v-col cols="12" md="4">
          <v-text-field v-model.number="hips" :rules="hipsRules" label="Fianchi diametro (cm)"></v-text-field>
        </v-col>
	<v-col v-for="(item, index) in bodyQuestions" v-bind:key="index" cols="12" md="6">
	  <v-text-field v-model.number="bodyAnswers[index]" :rules="bodyRules" :label="bodyQuestions[index]"></v-text-field>
        </v-col>
      </v-row>
      </div>
      <div v-show="e1 >= 3" ref="subform3" class="subform">
      <h2>Attività fisica</h2>
      <v-divider></v-divider>
      <v-row>
	<v-col v-for="(item, index) in activityQuestions" v-bind:key="index" cols="12" md="12">
      	  <span>{{item}}</span>
          <v-select v-model="activityAnswers[index]" :items="activityOptions[index]" :rules="[v => activityOptions[index].includes(v) || 'Item is required']" label="Select" required single-line></v-select>
        </v-col>
      </v-row>
      </div>
      <div v-show="e1 >= 4" ref="subform4" class="subform">
      <h2>Postura</h2>
      <v-divider></v-divider>
      <v-row>
	<v-col v-for="(item, index) in postureQuestions" v-bind:key="index" cols="12" md="12">
      	  <span>{{item}}</span>
          <v-select v-model="postureAnswers[index]" :items="postureOptions[index]" :rules="[v => postureOptions[index].includes(v) || 'Item is required']" label="Select" required single-line></v-select>
        </v-col>
      </v-row>
      </div>
  </v-form>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import longevityComputer from "../mixins/longevityComputer.js";
import loadFromStore from "../mixins/loadFromStore.js";
import round from "lodash/round";

export default {
    mixins: [longevityComputer, loadFromStore],
    data: () => ({
	e1: 1,
	subforms: null,
	valid: false,
	sex: null,
	sexOptions: ['uomo', 'donna',],
	bodyType: null,
	bodyOptions: ['mela', 'pera', 'melanzana',],
	height: null,
	heightRules: [v => v >= 60 && v <= 240 || 'Height must be in between 60 and 240 cm'],
	weight: null,
	weightRules: [v => v >= 20 && v <= 300 || 'Weight must be in between 20 and 300 Kg'],
	neck: '',
	neckRules: [v => v === '' || /^\d+$/.test(v) && v >= 30  && v <= 70 || 'Neck size must be between 30 and 70 cm'],
	waist: '',
	waistRules: [v => v === '' || /^\d+$/.test(v) && v >= 60 && v <= 110 || 'Waist size must be between 60 and 110 cm'],
	hips: '',
	hipsRules: [v => v === '' || /^\d+$/.test(v) && v >= 85 && v <= 130 || 'Hips size must be between 85 and 130 cm'],
	// Dynamically generated input
	bodyQuestions: [
	    "Radice coscia sinistra (cm)",
	    "Radice coscia destra (cm)",
	    "Sopra ginocchio sinistro (cm)",
	    "Sopra ginocchio destro (cm)",
	    "Polpaccio medio sinistro (cm)",
	    "Polpaccio medio destro (cm)",
	    "Caviglia sinistra (cm)",
	    "Caviglia destra (cm)",
	],
	bodyAnswers: [],
	bodyRules: [v => v === '' || /^\d+$/.test(v) || 'Measure must contain only numbers'],
	activityQuestions: [
	    "Che tipo di attività lavorativa svolgi?",
	    "Quanto tempo in media ti sposti a piedi con passo sostenuto e/p bicicletta durante la giornata nel tempo libero?",
	    "Quanto tempo spendi seduto/sdraiato durante la giornata fuori dal lavoro (ad esempio davanti al computer/televisione)?",
	    "Quante ore dedichi alla settimana ad attività di media intensità nel tempo libero (giardinaggio, yoga, pulizie intense, golf, camminate veloci)?",
	    "Pratichi attività sportiva intensa nel tuo tempo libero (corsa, calcio, palestra, nuoto)?",
	],
	activityOptions: [
	    ["intensa", "media", "bassa", "disoccupato/studente"],
	    ["Più di 60 minuti", "tra 30 e 60 minuti", "tra 15 e 30 minuti", "meno di 15 minuti"],
	    ["meno di 1h", "tra 1 e 3 ore", "tra 3 e 5 ore", "più di 5 ore"],
	    [">4h", "tra 3 e 4 ore", "tra 2 e 3 ore", "tra 1 e 2 ore", "meno di 1 ora"],
	    ["più di 3 volte a settimana", "3 volte a settimana", "2 volte a settimana", "1 volta a settimana", "occasionalmente", "mai/raramente"],
	],
	activityAnswers: [],
	postureQuestions: [
	    "Dismetria arti inferiori",
	    "Triangolo della taglia",
	    "Morfologia del piede",
	    "Traumi o interventi chirurgici importanti",
	    "Dolori alle articolazioni",
	    "Frequenza dei dolori alle articolazioni",
	    "Problematiche rachide",
	    "Rachide, frequenza dei dolori",
	    "Trattamenti farmacologici e/o manipolazioni per alleviare i dolori",
	],
	postureOptions: [
	    ["no/non so", "sì/compensato", "sì/non compensato"],
	    ["simmetrico", "asimmetrico"],
	    ["normale", "pronato", "supinato"],
	    ["0", "1", "2", "3 o più"],
	    ["nessuna", "piedi", "caviglie", "ginocchia", "anche", "altro"],
	    ["mai", "qualche episodio annuale", "qualche episodio mensile", "cronico"],
	    ["nessuna", "lombalgia", "dorsalgia", "cervicalgia", "sciatalgia"],
	    ["mai", "qualche episodio annuale", "qualche episodio mensile", "cronico"],
	    ["raramente", "una/due volte l'anno", "mensilmente", "con frequenza continuativa"],
	],
	postureAnswers: [],
	// Variables to be computed
	age: null,
	longevity: null,
	bmi: null, // Body mass index
	bmr: null, // Basal metabolic rate
	bfp: null, // Fat body mass
	whr: null, // Waist to hip ratio
	lbm: null, // Lean body Mass -> massa magra
	wid: null, // Water intake per day -> fabbisogno idrico
	pal: null, // Physical activity level -> laf -> livello attivita' fisica
	kcal: null, // Advised calories according to bmr and pal
	ibw: null, // Ideal body weight
    }),
    methods: {
	computeAge(dateString) {
	    let today = new Date();
	    let birthDate = new Date(dateString);
	    let age = today.getFullYear() - birthDate.getFullYear();
	    let m = today.getMonth() - birthDate.getMonth();
	    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
		age--;
	    }
	    return age;
	},
	isMale() {
	    return this.sex === this.sexOptions[0];
	},
	isFemale() {
	    return this.sex === this.sexOptions[1];
	},
	doesPhysicalActivity() {
	    return this.activityOptions[4].indexOf(this.activityAnswers[4]) < 4;
	},
	isLightActivity(longevity) {
	    return longevity < 0.33 ? true : false;
	},
	isMediumActivity(longevity) {
	    return longevity >= 0.33 && longevity < 0.66 ? true : false;
	},
	isHighActivity(longevity) {
	    return longevity >= 0.66 ? true : false;
	},
	computeBmi() {
	    return round(this.weight / Math.pow(this.height/100, 2), 1);
	},
	computeBmr(age) {
	    // Computing BMR using Harris-Benedict equation
	    if(this.isMale()) {
		return round(66.5 + (13.75 * this.weight) + (5.003 * this.height) - (6.755 * age));
	    }
	    return round(655.1 + (9.563 * this.weight) + (1.850 * this.height) - (4.676 * age));
	},
	computeWhr() {
	    if(this.waist != '' && this.hips != '') {
		return round(this.waist / this.hips, 2);
	    }
	    return null;
	},
	computeBodyType() {
	    // Assigning an objective body type if whr is present
	    if(this.whr === null) {
		return this.bodyType;
	    }
	    if(this.isMale() && this.whr > 0.95 || this.isFemale() && this.whr > 0.85) {
		return this.bodyOptions[0];
	    } else if(this.isMale() && this.whr < 0.95 || this.isFemale() && this.whr < 0.85) {
		return this.bodyOptions[1];
	    }
	    return this.bodyOptions[2];
	},
	computeBfp() {
	    if(this.whr === null || this.neck === '') {
		return null;
	    }
	    if(this.isMale()) {
		return round(495 / (1.0324 - 0.19077 * Math.log10(this.waist - this.neck) + 0.15456 * Math.log10(this.height)) - 450, 1);
	    }
	    return round(495 / (1.29579 - 0.35004 * Math.log10(this.waist + this.hips - this.neck) + 0.22100 * Math.log10(this.height)) - 450, 1);
	},
	computeLbm() {
	    if(this.isMale()) {
		return round((1.10 * this.weight) - 128 * (Math.pow(this.weight, 2)/ Math.pow(this.height, 2)), 1);
	    }
	    return round((1.07 * this.weight) - 148 * (Math.pow(this.weight, 2)/ Math.pow(this.height, 2)), 1);
	},
	computeWid() {
	    return round(this.weight * 0.03 - 0.7, 2); // where 700 cc is the water intaken from food
	},
	computePal() {
	    let longevity = this.computeLongevity(this.activityAnswers, this.activityOptions);
	    if(this.isMale()) {
		if(this.age <= 13) { return 1.65; }
		if(this.age <= 17) { return 1.58; }
		if(this.age <= 59) {
		    if(this.doesPhysicalActivity()) {
			if(this.isLightActivity(longevity)) { return 1.55; }
			if(this.isMediumActivity(longevity)) { return 1.78; }
			return 2.10;
		    } else {
			if(this.isLightActivity(longevity)) { return 1.41; }
			if(this.isMediumActivity(longevity)) { return 1.70; }
			return 2.01;
		    }
		}
		if(this.age <= 74) {
		    if(this.doesPhysicalActivity()) { return 1.51; }
		    return 1.40;
		}
		if(this.doesPhysicalActivity()) { return 1.51; }
		return 1.33;
	    } else {
		if(this.age <= 13) { return 1.55; }
		if(this.age <= 17) { return 1.50; }
		if(this.age <= 59) {
		    if(this.doesPhysicalActivity()) {
			if(this.isLightActivity(longevity)) { return 1.56; }
			if(this.isMediumActivity(longevity)) { return 1.64; }
			return 1.82;
		    } else {
			if(this.isLightActivity(longevity)) { return 1.42; }
			if(this.isMediumActivity(longevity)) { return 1.56; }
			return 1.73;
		    }
		}
		if(this.age <= 74) {
		    if(this.doesPhysicalActivity()) { return 1.56; }
		    return 1.44;
		}
		if(this.doesPhysicalActivity()) { return 1.56; }
		return 1.37;
	    }
	},
	computeKcal() {
	    return Math.round(this.bmr * this.pal);
	},
	computeIbw() {
	    // Calculating with Keys formula
	    if(this.isMale) {
		return round(Math.pow(this.height, 2) * 0.00221, 2);
	    }
	    return round(Math.pow(this.height, 2) * 0.00206, 2);
	},
	computeResult() {
	    this.age = this.computeAge(this.$store.state.registration.birthdate);
	    this.bmi = this.computeBmi();
	    this.bmr = this.computeBmr(this.age);
	    this.whr = this.computeWhr();
	    this.bodyType = this.computeBodyType();
	    this.bfp = this.computeBfp();
	    this.lbm = this.computeLbm();
	    this.wid = this.computeWid();
	    this.pal = this.computePal();
	    this.kcal = this.computeKcal();
	    this.ibw = this.computeIbw();
	    this.longevity = this.computeLongevity(this.activityAnswers, this.activityOptions);
	},
	aggregateMultipleChoice(questions, answers) {
	    return {"questions": questions, "answers": answers};
	},
	validate() {
	    if(this.e1 < this.subforms.length) {
		this.e1 += 1;
		this.$nextTick(() => this.subforms[this.e1 -1].scrollIntoView(true));
		return;
	    }
            if (this.$refs.form.validate()) {
		this.snackbar = true // to be removed -> no snackbar to show
		this.computeResult();
		let result = (({ sex, bodyType, weight, height, neck, waist, hips, bmi, bmr, bfp, whr, lbm, wid, pal, kcal, ibw, bodyAnswers, activityAnswers, postureAnswers}) =>
			      ({ sex, bodyType, weight, height, neck, waist, hips, bmi, bmr, bfp, whr, lbm, wid, pal, kcal, ibw, bodyAnswers, activityAnswers, postureAnswers}))(this);
		// Adding additional properties before committing
		// result["additionalBodyMeasures"] = this.aggregateMultipleChoice(this.bodyQuestions, this.bodyAnswers);
		// result["activity"] = this.aggregateMultipleChoice(this.activityQuestions, this.activityAnswers);
		// result["posture"] = this.aggregateMultipleChoice(this.postureQuestions, this.postureAnswers);
		this.$store.commit('setBody', result);
            }
	},
    },
    created() {
	this.bodyAnswers = Array(this.bodyQuestions.length).fill('');
    },
    computed: {
	    ...mapState(['body']),
	    ...mapGetters(['getBirthdate']),
    },
    mounted() {
	// Loading registration values from vuex if already present
	this.loadFromStore(this.$data, this.body);
	this.subforms = [this.$refs.subform1, this.$refs.subform2, this.$refs.subform3, this.$refs.subform4];
    },
    watch: {
	getBirthdate(newValue, oldValue) {
	    if(this.valid && newValue !== oldValue) {
		this.validate();
	    }
	}
    },
  }
</script>
