<!-- Dati Antropometrici -->
<template>
<v-form v-model="valid" ref="form">
      <v-row>
	<v-col v-for="(item, index) in questions" v-bind:key="index" cols="12" md="12">
	  <span>{{item}}</span>
          <v-select v-model="stressAnswers[index]" :items="options" :rules="[v => !!v || 'Item is required']" label="Select" required single-line></v-select>
        </v-col>
      </v-row>
  </v-form>
</template>

<script>
import { mapState } from 'vuex';
import longevityComputer from "../mixins/longevityComputer.js"
import loadFromStore from "../mixins/loadFromStore.js"
  
export default {
    mixins: [longevityComputer, loadFromStore],
    data: () => ({
	valid: false,
	questions: [
	    "Durante il giorno sono spesso stanco e spossato, anche se la notte prima ho dormito abbastanza.",
	    "Se nel lavoro qualcosa non va come avevo immaginato, reagisco spesso con rassegnazione.",
	    "Ho problemi a staccarmi dal lavoro la sera, durante i fine settimana o quando sono in ferie.",
	    "Sono più soggetto che in passato a infezioni come raffreddori o influenze.",
	    "Mi concentro con difficoltà, perdo spesso il filo durante i discorsi, mi faccio distrarre dalle interruzioni e fatico poi a ritrovare la concentrazione.",
	    "Quando si verifica un problema spesso divento nervoso e reagisco in maniera aggressiva.",
	    "Ultimamente soffro più spesso di mal di testa, mal di stomaco e tachicardia.",
	    "A causa del poco tempo a disposizione, delle responsabilità e dei molti impegni da sbrigare mi sento spesso come il famoso “criceto sulla ruota”, che corre sempre, ma non arriva mai da nessuna parte.",
	    "Non frequento molto gli amici ed evito le situazioni sociali. Preferisco la mia tranquillità.",
	    "Bevo più di tre caffè al giorno.",
	    "Evito il più delle volte il contatto con i miei colleghi, perché mi porta via troppo tempo o perché mi costa troppa fatica.",
	    "Il ronzio di una mosca o il telefono che squilla mi innervosiscono più che in passato.",
	    "Ho problemi ad addormentarmi e/o mi sveglio spesso durante la notte e rimugino sui problemi del lavoro.",
	    "Ultimamente ho la tendenza a dimenticare o smarrire le cose importante",
	    "Spesso percepisco una sorta di vuoto interiore e faccio fatica a ricordare l’ultima volta in cui ho provato un profondo senso di felicità.",
	],
	stressAnswers: [],
	options: ['falso', 'quasi vero', 'vero',],
	// scores: [0, 0.50, 1],
	longevity: 0,
	resultStress: 'vero',
    }),
    methods: {
	computeResult() {
	    let frequencyDist = new Array(this.options.length).fill(0);
	    // let scoreSum = 0;
	    let answersNo = this.stressAnswers.length;
	    for(let i = 0; i < answersNo; i++) {
		frequencyDist[this.options.indexOf(this.stressAnswers[i])] += 1;
		// scoreSum += this.scores[this.options.indexOf(this.stressAnswers[i])];
	    }
	    let maxEl = Math.max(...frequencyDist);
	    let maxElNo = 0;
	    for(let i = 0; i < frequencyDist.length; i++) {
		if(frequencyDist[i] === maxEl) {
		    maxElNo += 1;
		}
	    }
	    if(maxElNo <= 1) {
		this.resultStress = this.options[frequencyDist.indexOf(maxEl)];
	    } else {
		this.resultStress = 'nessuna maggioranza';
	    }
	    // this.longevity = scoreSum / answersNo;
	    this.longevity = this.computeLongevity(this.stressAnswers, this.options);
	},
	validate() {
            if (this.$refs.form.validate()) {
		this.snackbar = true;
		this.computeResult();
		let result = (({ resultStress, stressAnswers }) => ({ resultStress, stressAnswers }))(this);
		this.$store.commit('setStress', result);
            }
	},
    },
    computed: {
	    ...mapState(['stress']),
    },
    mounted() {
	// Loading stress values from vuex if already present
	this.loadFromStore(this.$data, this.stress);
    },
}
</script>
