<!-- Detox Test -->
<template>
<v-form v-model="valid" ref="form">
      <v-row>
	<v-col v-for="(item, index) in questions" v-bind:key="index" cols="12" md="12">
	  <span>{{item}}</span>
          <v-select v-model="detoxAnswers[index]" :items="options" :rules="[v => !!v || 'Item is required']" label="Select" required single-line></v-select>
        </v-col>
      </v-row>
  </v-form>
</template>

<script>
import { mapState } from 'vuex';
import longevityComputer from "../mixins/longevityComputer.js"
import loadFromStore from "../mixins/loadFromStore.js"
  
export default {
    mixins: [longevityComputer, loadFromStore],
    data: () => ({
	valid: false,
	questions: [
	    "Siete fumatori, avete fumato in passato o siete fumatori passivi?",
	    "Soffrite di gonfiore addominale e cattiva digestione?",
	    "Sentite spesso il bisogno di mangiare dolci?",
	    "Avete grasso addominale in eccesso (la classica pancetta) o cellulite?",
	    "Siete colpiti da raffreddore o influenza più di due volte all’anno?",
	    "Russate, avete apnee o dormite meno di 6 ore per notte?",
	    "Mangiate carne rossa più di una volta al mese?",
	    "Bevete meno di un litro e mezzo di acqua al giorno?",
	    "Avete l’alito cattivo o la patina sulla lingua?",
	    "Soffrite di disturbi gastrici o avete problemi intestinali (stipsi, diarrea, colite)?",
	    "Mangiate abitualmente salato?",
	    "Vi sentite ansiosi, stressati, depressi?",
	    "Consumate cibi confezionati o raffinati, frequentate i fast food anche più volte alla settimana?",
	    "Vivete o lavorate in un ambiente inquinato?",
	    "Vi sentite gli occhi gonfi?",
	    "Fate uso assiduo di farmaci?",
	],
	detoxAnswers: [],
	options: ['no', 'sì',],
	resultDetox: '',
	longevity: 0,
    }),
      methods: {
	  updateResultDetox() {
	      this.longevity = this.computeLongevity(this.detoxAnswers, this.options);
	      if(this.longevity <= 1 - 10 / this.questions.length) {
		  this.resultDetox = "alto";
	      } else if(this.longevity < 1 - 4 / this.questions.length) {
		  this.resultDetox = "medio";
	      } else {
		  this.resultDetox = "basso";
	      }
	  },
	  validate() {
              if (this.$refs.form.validate()) {
		  this.updateResultDetox();
		  let result = (({ resultDetox, detoxAnswers }) => ({ resultDetox, detoxAnswers }))(this);
		  this.$store.commit('setDetox', result);
              }
	  },
      },
    computed: {
	    ...mapState(['detox']),
    },
    mounted() {
	// Loading detox values from vuex if already present
	this.loadFromStore(this.$data, this.detox);
    },
  }
</script>
