<template>
<v-app>
  <v-container>
    <v-col>
      <h2>Search users</h2>
      <v-form v-model="valid" ref="formCode" @submit.prevent>
	<v-row>
	  <v-col cols="12">
	    <v-text-field v-model="fcode" :rules="nameRules" label="Codice fiscale" required></v-text-field>
	  </v-col>
	  <v-col cols="12" md="6">
	    <v-text-field v-model="fname" :rules="nameRules" :counter="30" label="Nome" required></v-text-field>
	  </v-col>
	  <v-col cols="12" md="6">
	    <v-text-field v-model="lname" :rules="nameRules" :counter="30" label="Cognome" required></v-text-field>
	  </v-col>
	</v-row>
      </v-form>
      <v-col>
	<v-row justify="end">
	  <v-btn color="primary" @click="()=>validate()">Search</v-btn>
	</v-row>
      </v-col>
    </v-col>
    <!-- <p>{{this.query}}</p> -->
    <v-col>
      <h2 v-show="users.length > 0">Results</h2>
    <v-row>
      <v-col v-for="(user, index) in users" :key="index" cols="12">
	<v-hover v-slot:default="{ hover }">
	<v-card class="mx-auto" :elevation="hover ? 6 : 2" @click="viewUser(user.fiscalCode)">
	  <v-card-title><strong>{{user.firstname}} {{user.lastname}}</strong></v-card-title>
	  <v-list-item>Codice fiscale: {{user.fiscalCode}}</v-list-item>
	</v-card>
	</v-hover>
      </v-col>
    </v-row>
    </v-col>
  </v-container>
</v-app>
</template>

<script>

export default {
    props: {'firstname': String, 'lastname': String, 'fiscalCode': String},
    data: () => ({
	valid: false,
	fcode: '',
	fname: '',
	lname: '',
	nameRules: [ v => v.length <= 30 || 'Text must be less than 30 characters'],
	users: [],
    }),
    methods: {
	searchUser(query) {
	    let self = this;
	    this.$http.get('/findusers', {params: query})
		.then(response => {
		    let users = response.data;
		    self.users = users;
		})
		.catch(() => {

		});
	},
	viewUser(fiscalCode) {
	    this.$router.push({ name: 'user', params: {id: fiscalCode} });
	},
	validate() {
	    // entering query only if different then previous one -> avoiding navigation error
	    if(this.fname !== this.firstname || this.lname !== this.lastname || this.fcode !== this.fiscalCode) {
		let query = {"firstname": this.fname, "lastname": this.lname, 'fiscalCode': this.fcode};
		this.$router.push({ path: 'about', query: query});
		this.searchUser(query);
	    }
	},
    },
    computed: {
	query: function () {
	    return this.$props;
	},
    },
    created() {
	// loading query from url if present
	if(this.firstname !== undefined || this.lastname !== undefined || this.fiscalCode !== undefined) {
	    this.fname = this.firstname;
	    this.lname = this.lastname;
	    this.fcode = this.fiscalCode;
	    this.searchUser(this.$props);
	}
    },
}
</script>
