<!-- Detox Test -->
<template>
<v-form v-model="valid" ref="form">
  <p>Che probabilità ha di appisolarsi o di addormentarsi nelle seguenti situazioni, indipendentemente dalla sensazione di stanchezza?
    La domanda si riferisce alle usuali abitudini di vita nell'ultimo periodo.
    Qualora non si sia trovato di recente in alcune delle situazioni elencate sotto, provi ad immaginare come si sentirebbe.
    Usi la seguente scala per scegliere il punteggio più adatto ad ogni situazione:</p>
  <ul>
    <li>0 = non mi addormento mai</li>
    <li>1 = qualche probabilità di addormentarmi</li>
    <li>2 = discreta probabilità di addormentarmi</li>
    <li>3 = alta probabilità di addormentarmi</li>
  </ul>
  <br />
  <v-divider></v-divider>
  <v-row>
    <v-col v-for="(item, index) in questions" v-bind:key="index" cols="12" md="12">
      <span>{{item}}</span>
      <v-select v-model.number="sleepAnswers[index]" :items="options" :rules="[v => options.includes(v) || 'Item is required']" label="Select" required single-line></v-select>
    </v-col>
  </v-row>
</v-form>
</template>

<script>
import { mapState } from 'vuex';
import longevityComputer from "../mixins/longevityComputer.js"
import loadFromStore from "../mixins/loadFromStore.js"
  
export default {
    mixins: [longevityComputer, loadFromStore],
    data: () => ({
	valid: false,
	questions: [
	    "Seduto mentre leggo",
	    "Guardando la TV",
	    "Seduto, inattivo in un luogo pubblico (a teatro, ad una conferenza) ",
	    "Passeggero in automobile, per un'ora senza sosta",
	    "Sdraiato per riposare nel pomeriggio, quando ne ho l'occasione",
	    "Seduto mentre parlo con qualcuno",
	    "Seduto tranquillamente dopo pranzo, senza avere bevuto alcoolici",
	    "In automobile, fermo per pochi minuti nel traffico",
	],
	sleepAnswers: [],
	options: [0, 1, 2, 3],
	longevity: 0,
	resultSleep: 0,
    }),
      methods: {
	  updateResultSleep() {
	      let answersNo = this.sleepAnswers.length;
	      this.resultSleep = 0;
	      for(let i = 0; i < answersNo; i++) {
		  this.resultSleep += this.sleepAnswers[i];
	      }
	      this.longevity = this.computeLongevity(this.sleepAnswers, this.options);
	  },
	  validate() {
              if (this.$refs.form.validate()) {
		  this.updateResultSleep();
		  let result = (({ resultSleep, sleepAnswers }) => ({ resultSleep, sleepAnswers }))(this);
		  this.$store.commit('setSleep', result);
              }
	  },
      },
    computed: {
	    ...mapState(['sleep']),
    },
    mounted() {
	// Loading sleep values from vuex if already present
	this.loadFromStore(this.$data, this.sleep);
    },
}
</script>
