import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";
import isEmpty from "lodash/isEmpty";

Vue.use(Vuex);
const vuexLocal = new VuexPersistence({
  storage: window.localStorage
});

export default new Vuex.Store({
    state: {
	// Test results
	registration: {},
	privacy: {},
	objective: {},
	body: {},
	skin: {},
	detox: {},
	stress: {},
	sleep: {},
	nutrition: {},
	// Additional values
	optionsScore: {},
	controlPanel: {},
	supplements: {},
	online: {}
    },
    mutations: {
	setRegistration(state, x) {
	    state.registration = x;
	},
	setPrivacy(state, x) {
	    state.privacy = x;
	},
	setObjective(state, x) {
	    state.objective = x;
	},
	setBody(state, x) {
	    state.body = x;
	},
	setSkin(state, x) {
	    state.skin = x;
	},
	setDetox(state, x) {
	    state.detox = x;
	},
	setStress(state, x) {
	    state.stress = x;
	},
	setSleep(state, x) {
	    state.sleep = x;
	},
	setNutrition(state, x) {
	    state.nutrition = x;
	},
	setOptionsScore(state, optLength) {
	    if (typeof state.optionsScore[optLength] != 'undefined') {
		return;
	    }
	    let arr = [];
	    let start = 1;
	    let stop = 0;
	    var step = (stop - start) / (optLength - 1);
	    for (let i = 0; i < optLength; i++) {
		arr.push(Math.round((start + (step * i)) * 100) / 100);
	    }
	    state.optionsScore[optLength] = arr;
	},
	setControlPanel(state, x) {
	    state.controlPanel = x;
	},
	popSupplement(state, index) {
	    state.supplements[index] = "";
	},
	setSupplements(state, x) {
	    state.supplements = x;
	},
	setOnline(state, x) {
	    state.online = x;
	},
	deleteEverything(state) {
	    for(let key in state) {
		if(state.hasOwnProperty(key)) {
		    delete state[key];
		}
	    }
	}
    },
    getters: {
	getScoreFromOptions: state => (optLength, index) => {
	    return state.optionsScore[optLength][index];
	},
	getSubmitData: state => {
	    return (({ registration, privacy, objective, body, skin, detox, stress, sleep, nutrition }) =>
		    ({ registration, privacy, objective, body, skin, detox, stress, sleep, nutrition }))(state);
	},
	getBirthdate: state => {
	    return state.registration.birthdate;
	},
	hasSupplement: state => sup => {
	    if(isEmpty(state.supplements)) {
		return -1;
	    }
	    return state.supplements.indexOf(sup);
	}
    },
    actions: {},
    modules: {},
    plugins: [vuexLocal.plugin]
});
